<template>
  <div class="passwordLogin">
    <el-form ref="loginForm" :rules="Rules" :model="loginForm" autocomplete="on">
      <el-form-item prop="userName" status-icon>
        <el-input
          v-model="loginForm.userName"
          spellcheck="false"
          autocomplete="on"
          placeholder="请输入手机号/用户名"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          ref="password"
          v-model="loginForm.password"
          :type="pwdType"
          autocomplete="on"
          spellcheck="false"
          placeholder="请输入密码"
        ></el-input>
        <span class="show-pwd" @click="showPwd">
          <svg-icon icon-class="eye" />
        </span>
      </el-form-item>
      <el-form-item prop="sidentify" class="sidentify-wrapper">
        <div class="item-wrapper">
          <div class="el-input-wrapper">
            <el-input placeholder="请输入图形验证码" v-model="loginForm.sidentify"></el-input>
          </div>
          <div
            class="login-code"
            @click="refreshCode"
            style="width:120px;height:40px;border: 1px solid #d7dae2;border-radius: 4px;overflow: hidden;box-sizing:border-box;"
          >
            <el-image style="width:100%;height:100%;" :src="refresh.b64s" lazy></el-image>
          </div>
        </div>
      </el-form-item>
      <el-form-item class="submit-wrapper">
        <button class="submit" type="button" @click="submitForm('loginForm',$event)">登录</button>
      </el-form-item>
      <el-form-item class="desc-wrapper">
        <div style="flex:1">
          <el-checkbox v-model="checked" style="color:#a0a0a0;">记住密码</el-checkbox>
        </div>
        <div style="flex:1;display: inline-flex;justify-content: flex-end;">
          <router-link to="retrieve" class="text-wrapper">忘记密码</router-link>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  password_login,
  getCode,
  setEncryption,
  artificial_code,
} from "@/mixins/user";
export default {
  name: "PasswordLogin",
  mixins: [password_login, getCode, setEncryption, artificial_code],
};
</script>


