<template>
  <div class="wrchat">
    <div class="body-wrapper">
      <el-image style="width: 260px; height: 260px" :src="url"></el-image>
    </div>
    <div class="third-party">
      <div class="icon-wrapper">
        <div class="icon left">
          <i class="icon-48-38-60-47 iconfont"></i>
        </div>
        <!-- <div class="icon right">
          <i class="icon-48-38 iconfont"></i>
        </div>-->
      </div>
      <div class="title-wrapper">客户端扫码登录</div>
    </div>
    <div class="footer-wrapper">
      <a @click="previousPage('password')">密码登录</a>
      <el-divider direction="vertical"></el-divider>
      <a @click="previousPage('sms')">验证码登录</a>
    </div>
  </div>
</template>

<script>
import { getQrcode, qrStatus } from "@/api/code";
import App from "@/utils/cofigApp";
export default {
  name: "WechatLogin",
  components: {},
  props: {
    isInit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      url: "",
      scene_str: null,
      id: null,
      qid: null,
      sid: null,
      state: false,
    };
  },
  watch: {
    isInit(value) {
      if (value) {
        let _this = this;
        this.sid = setTimeout(() => {
          if (!this.state) {
            this.previousPage("sms");
          }
        }, 60000 * 1);
        this.id = setInterval(() => {
          this.$store
            .dispatch("wechat_login", {
              appid: this.appid,
              scene_str: this.scene_str,
            })
            .then((res) => {
              // console.log(res);
              _this.state = true;
              _this.clearTiming();
              _this.$message({
                message: "未绑定手机号，需先绑定手机号",
                type: "warning",
              });
              _this.$router.push({
                name: "bind",
                params: { uuid: res.wechat.id, code: res.disposable_code },
              });
            })
            .catch((error) => {
              _this.$router.push({ name: "account" });
            });
        }, 1500);
      } else {
        this.state = false;
      }
    },
  },
  computed: {
    appid() {
      return App.appid;
    },
  },
  methods: {
    qrcode() {
      let self = this;
      getQrcode({ appid: this.appid }).then((res) => {
        const data = res.data;
        // console.log(data)
        self.url = data.url;
        self.scene_str = data.scene;
      });
    },
    previousPage(name) {
      // console.log(name)
      if (this.id !== null) {
        this.clearTiming();
      }
      this.$emit("previousPage", name);
    },
    clearTiming() {
      clearTimeout(this.qid);
      clearTimeout(this.sid);
      clearInterval(this.id);
    },
  },
  created() {
    this.$nextTick((res) => {
      this.qrcode();
      this.qid = setInterval(() => {
        this.qrcode();
      }, 60000 * 4);
    });
  },
  // mounted() {},
  beforeRouteLeave(to, form, next) {
    if (this.id !== null) {
      this.clearTiming();
    }
    next();
  },
  beforeDestroy() {
    if (this.id !== null) {
      this.clearTiming();
    }
  },
};
</script>
<style lang="scss" scoped>
@import "../../../../../styles/global";
// @import "../../../assets/styles/pc/index";
.wrchat {
  width: 100%;
  height: 500px !important;
  box-sizing: border-box;
  padding: 0 35px;
  display: flex;
  flex-direction: column;
  .head-wrapper {
    text-align: right;
    color: #333333;
    cursor: pointer;
    a {
      &:visited {
        color: #4c84c1;
        border-bottom: 1px solid #4c84c1;
      }
      &:hover {
        color: #4c84c1;
      }
      &:active {
        color: #4c84c1;
      }
    }
  }
  .body-wrapper {
    flex-grow: 5;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .third-party {
    flex-grow: 2;
    display: flex;
    width: 100%;
    justify-content: center;
    .icon-wrapper {
      box-sizing: border-box;
      display: flex;
      flex-wrap: wrap;
      // justify-content: space-between;
      justify-content: center;
      margin-right: 15px;
      .icon {
        width: 50px;
        height: 50px;
        @include center;
        border-radius: 50%;
        cursor: pointer;
        box-sizing: border-box;
        .iconfont {
          font-size: 35px;
          color: #22ac38;
        }
      }
      .left {
        background-color: #f2f2f2;
        padding-top: 5px;
      }
      .right {
        background-color: #73befb;
      }
    }
    .title-wrapper {
      font-size: 16px;
      letter-spacing: 1px;
      color: #333333;
      margin-top: 15px;
    }
  }
  .footer-wrapper {
    flex-grow: 1;
    text-align: center;
    a {
      cursor: pointer;
    }
  }
}
</style>


