<template>
  <div class="content">
    <div class="center-wrapper">
      <div class="left-wrapper">
        <img src="../../../../static/image/pc/login_l.png" />
      </div>
      <div class="right-wrapper" v-show="!isWechat">
        <el-tabs v-model="activeName">
          <el-tab-pane label="短信登录" :lazy="true" name="sms">
            <sms-login @wechat="wechat" />
          </el-tab-pane>
          <el-tab-pane label="密码登录" :lazy="true" name="password">
            <password-login />
          </el-tab-pane>
        </el-tabs>
      </div>
      <wechat-login @previousPage="previousPage" :isInit="isInit" v-show="isWechat" />
    </div>
  </div>
</template>

<script>
import { SmsLogin, PasswordLogin, WechatLogin } from "./components";
export default {
  components: {
    SmsLogin,
    PasswordLogin,
    WechatLogin,
  },
  data() {
    return {
      activeName: "sms",
      isWechat: false,
      isInit: false,
    };
  },
  methods: {
    wechat() {
      this.isWechat = true;
      this.isInit = true;
    },
    previousPage(name) {
      this.activeName=name
      // console.log(name)
      this.isWechat = false;
      this.isInit = false;
    },
  },
  created() {
    this.$emit("customStyle", 0);
  },
};
</script>
<style lang="scss" scoped>
@import "../../../styles/global";
@import "../../../assets/styles/pc/index";
.content {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  background: url("../../../../static/image/pc/login_bg.png") no-repeat;
  background-size: cover;
  color: #333;
  .center-wrapper {
    width: 950px;
    height: 550px;
    // border-radius: 4px;
    padding: 25px 35px;
    margin: 0 auto;
    background-color: #ffffff;
    box-sizing: border-box;
    box-shadow: 0px 0px 21px 0px rgba(13, 13, 13, 0.14);
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    > div {
      width: 50%;
      height: 100%;
      float: left;
      overflow: hidden;
    }
    > div:last-of-type {
      position: absolute;
      right: 0;
    }
    &::after {
      content: "";
      clear: both;
    }
    .left-wrapper {
      img {
        width: 100%;
        height: 100%;
      }
    }
    .right-wrapper {
      box-sizing: border-box;
      padding: 10px 25px 0 60px;
      /deep/ .el-tabs {
        .el-tabs__item {
          font-size: 18px;
          line-height: 40px;
          height: 40px;
          color: #999999;
        }
        .el-tabs__item:focus.is-active.is-focus:not(:active) {
          box-shadow: none;
          border-radius: 0;
        }
        .el-tabs__item.is-active {
          color: #4497ef;
        }
        .el-tabs__active-bar {
          background-color: #4497ef;
        }
        .el-tabs__nav-wrap::after {
          background-color: #eeeeee;
        }
        .el-tabs__content {
          font-size: 14px;
          padding: 15px 0 0;
          height: auto;
          box-sizing: border-box;
          @include form;
          .third-party {
            width: 100%;
            height: auto;
            text-align: center;
            padding-top: 5px;
            .icon-wrapper {
              width: 100%;
              height: auto;
              padding: 0 30%;
              box-sizing: border-box;
              display: flex;
              flex-wrap: wrap;
              // justify-content: space-between;
              justify-content: center;
              .icon {
                width: 50px;
                height: 50px;
                @include center;
                border-radius: 50%;
                cursor: pointer;
                box-sizing: border-box;
                .iconfont {
                  font-size: 35px;
                  color: #22ac38;
                }
              }
              .left {
                background-color: #f2f2f2;
                padding-top: 5px;
              }
              .right {
                background-color: #73befb;
              }
            }
            .title-wrapper {
              font-size: 16px;
              letter-spacing: 1px;
              color: #333333;
              margin-top: 15px;
            }
          }
        }
      }
    }
  }
}
</style>


