<template>
  <div class="SmsLogin">
    <el-form ref="loginForm" :model="loginForm" :rules="loginRules" status-icon>
      <el-form-item prop="phone">
        <el-input v-model="loginForm.phone" placeholder="请输入手机号"></el-input>
      </el-form-item>
      <el-form-item prop="sidentify" class="sidentify-wrapper" v-if="securityLevel>0">
        <div class="item-wrapper">
          <div class="el-input-wrapper">
            <el-input placeholder="请输入答案" v-model="loginForm.sidentify"></el-input>
          </div>
          <div
            class="login-code"
            @click="refreshCode"
            style="width:120px;height:40px;border: 1px solid #d7dae2;border-radius: 4px;overflow: hidden;box-sizing:border-box;"
          >
            <el-image style="width:100%;height:100%;" :src="refresh.b64s" lazy></el-image>
          </div>
        </div>
      </el-form-item>
      <el-form-item prop="code" class="smsCode-wrapper">
        <div class="item-wrapper">
          <div class="el-input-wrapper">
            <!-- <el-input
                      placeholder="请输入短信验证码"
                      v-model="loginForm.smsCode"
                      @keyup.enter.native="submitForm('loginForm')"
            ></el-input>-->
            <el-input
              placeholder="请输入短信验证码"
              v-model="loginForm.code"
              @keyup.enter.native="submitForm('loginForm',$event)"
            ></el-input>
          </div>
          <button type="button" :class="!show?'send-button_s block':'send-button block'">
            <span v-show="show" @click="getCode">发送验证码</span>
            <span v-show="!show" class="count">{{count}}s后重新发送</span>
          </button>
        </div>
      </el-form-item>
      <div class="item-text">未注册的手机号验证后自动创建账户</div>
      <el-form-item class="submit-wrapper">
        <button type="button" class="submit" @click="submitForm('loginForm',$event)">登录</button>
      </el-form-item>
      <el-form-item class="desc-wrapper" prop="key">
        <el-row :span="24" :gutter="17">
          <el-col :span="14">
            <el-checkbox v-model="loginForm.key">
              我已阅读并接受
              <router-link
                tag="span"
                :to="{path:'agreement',query:{name:'user'}}"
                class="yellow"
              >用户协议</router-link>&nbsp;和
              <router-link
                tag="span"
                :to="{path:'agreement',query:{name:'policy'}}"
                class="yellow"
              >隐私政策</router-link>
            </el-checkbox>
          </el-col>
          <!-- <el-col :span="8" :style="{textAlign:'right'}" :offset="2">
            <router-link to="institutions" class="text-wrapper">组织用户登录</router-link>
          </el-col>-->
        </el-row>
      </el-form-item>
    </el-form>
    <div class="third-party">
      <div class="icon-wrapper">
        <div class="icon left" @click="wechat">
          <i class="icon-48-38-60-47 iconfont"></i>
        </div>
        <!-- <div class="icon right">
          <i class="icon-48-38 iconfont"></i>
        </div>-->
      </div>
      <div class="title-wrapper">客户端扫码登录</div>
    </div>
  </div>
</template>

<script>
import { text_login, getCode, artificial_code } from "@/mixins/user";
export default {
  name: "SmsLogin",
  mixins: [text_login, getCode, artificial_code]
};
</script>


